import React, { Component, Suspense } from 'react';
import PropTypes from 'prop-types';
const MainBlockRecipeHeader = React.lazy(() => import('./MainBlock-Recipe-Components/MainBlock-recipe-header'));
const MainBlockRecipeContent = React.lazy(() => import('./MainBlock-Recipe-Components/MainBlock-recipe-content'));

/*
* Компонент загружающий header и контент со списком рецептов
*
* Содержит в себе 2 дочерних компонента: header и полный перечень рецептов
* Все данные для построения UI берутся из datalist.js и передаются пропсами в dataitems
* На вход принимает 2 аргумента:
* @input recipes - рецепты и подробная информация
* @input optionParametr - тип выводимой информации (0/1/2)
*/

export default class MainBlockRecipeRender extends Component {
  render() {
    return (
      <Suspense fallback={<div>Загрузка...</div>}>
        <main className="selection">
          <MainBlockRecipeHeader optionParametr={this.props.optionParametr} count={this.props.recipes.length}/>
          <MainBlockRecipeContent dataitems={this.props.recipes}/>
        </main>
      </Suspense>
    )
  }
}


MainBlockRecipeRender.propTypes = {
  dataitems: PropTypes.arrayOf(PropTypes.object),
};


